@use '../base/mixins' as m;
@use '../base/variables' as v;


.main{
  padding:10rem 0;

  &__title {
    text-align: left;
    font-weight: normal;
    @include m.encabezado(0,0);

    &__span {
      font-weight: bold;
    }
  }


}

.main__grid {
   margin-top: 3rem;

  @include m.tablet {
    @include m.grid (2, 1rem);
  }

  @include m.pc {
    @include m.grid (3, 1rem);
  }
}



.propiedad{
  background-color: v.$blanco;
  border-radius: 0 0 20px 20px;
  margin-bottom: 1rem;

  @include m.tablet {
    margin-bottom: 0
  }
 
  &__imagen {

    aspect-ratio: 8/6; 
    position: relative;
    background-size: auto 150%;

  }

  &__nombre {
    text-align: center;
    position: absolute;
    bottom: 0;
    left: 1.5rem;
    font-weight: bold;
    color: v.$blanco;
  }

 

  @for $i from 1 through 6 {
    $imagen: '../img/propiedad_'+$i+'.jpg';

    &:nth-child(#{$i}) &__imagen{
      background-image: linear-gradient(to bottom,transparent 0,rgba(0,0,0,.7)),url($imagen);
    }
   
  }


  &__info {
    border-radius: 0 0 10px 10px;
    border: 1.5px solid v.$azul;
    padding: 1rem 2rem;
  }

  &__text {
    text-align: justify;
  }

  &__precio {
    text-align: center;
    font-weight: bold;
    font-size: 2rem;
  }

  &__btn {
    @include m.btn (v.$azul, v.$blanco, v.$aOscuro);
  }

} 





