@use '../base/variables' as v;



html{
  font-size: 62.5%;
  box-sizing: border-box;
}
*:before,
*:after {
  box-sizing: inherit;
}

body {
  background-color: v.$gris;
  font-family: v.$principal;
  font-size: 1.6rem;

}
a {
  text-decoration: none;
  font-size: 2rem;
}
p{
  font-size: 1.5rem;
  line-height: 1.6;
  text-align: left;
}


h1,h2,h3 {
  margin:0 0 2.5rem;
}

h1 {
  font-size:4.5rem;
  margin-bottom: 3rem;
}

h2 {
  font-size: 3.5rem;
}

h3 {
  font-size: 2.2rem;
}

img {
  display: block;
  max-width:100%;
  height: auto;
  margin:0 auto;
  
}

span{
  font-weight: 600;
}

a{
  display: block;
  text-decoration: none;
  font-size: 1.8rem;
}

li {
 list-style: none;
}


.contenedor {
  width: 90%;
  max-width:120rem;
  margin: 0 auto;
  text-align: center;
}



