@use '../base/mixins' as m;
@use '../base/variables' as v;


.testimoniales-blog {
  padding: 5rem 0;
  
  @include m.tablet {
    @include m.grid(3, 3rem);
  }



}

.testimoniales-blog__testimonios {

}

.testimonios__title {
  font-weight: normal;
  
  @include m.encabezado(0,auto);

  .title__span {
    display: block;
  }

}

.testimonio {
  margin-bottom: 3rem;
  
  &__imagen {
    max-width:25rem;
  }

  &__info {
    background-color: v.$blanco;
    margin-top: -10rem;
    padding: 10rem 1rem 1rem 2.5rem ;
    border: 1px solid v.$aOscuro;

  }

  &__nombre {
    font-size: 2.3rem;
    text-align: center;
    font-weight: bold;
  }

  &__text {  

    &::before {
      background-image: url(../img/quote.svg);
      background-position: 90%;
      background-repeat: no-repeat;
      content: "";
      display: inline-block;
      height: 3.5rem;
      margin-right: 1rem;
      width: 3.5rem;
  }

  }
}




.testimoniales-blog__blog{
 grid-column: 2/4;


  .blog__title {
    @include m.encabezado(0,auto);
    font-weight: normal;

    .title__span {
      display: block;
    }
  }
}

.article {
  margin-bottom: 2rem;
  


  @include m.phone {
    @include m.grid (5, 0);
    align-items: center;
  }

  &__img {
    grid-column: 1/3;

  }

  &__imagen{
    border: 2px solid v.$aOscuro;
    height: 30rem;
    -o-object-fit: cover;
    object-fit: cover;
    width: 100%;
    position: relative;
    z-index: -1;
   
  }

  &__info {
    margin-left: .5rem;
    background-color: v.$blanco;
    border: 1px solid v.$azul;  
    grid-column: 3/6;
    margin-top: -3rem;
    padding: 2rem 1rem;
    z-index: 1;


    @include m.phone {
      margin-left: 0;
      padding: 3rem 2rem;
      margin-left: -2rem;
      margin-top: 0;
    }
  }

  &__title {
    margin-bottom: 1rem;
  }

  &__meta {
    margin: 0;
    font-size: 1.3rem;
  }

  &__text {

  }

  &__btn {
    @include m.btn(v.$azul, v.$blanco, v.$aOscuro)
  }
}

