@use 'variables' as v;


@mixin phone {
  @media(min-width: v.$phone){
    @content;
  }
}
@mixin tablet {
  @media(min-width: v.$tablet){
    @content;
  }
}

@mixin pc{
  @media(min-width: v.$pc){
    @content;
  }
}

@mixin monitor{
  @media(min-width: v.$monitor){
    @content;
  }
}


//display grid
@mixin grid($columnas,$gap){
  display:grid;
  grid-template-columns: repeat($columnas,1fr);
  gap:$gap;
}

@mixin encabezado($mAb,$mDi) {
  &:after{
    margin: $mAb $mDi;
    margin-top: 1rem;
    display: block;
    content:"";
    width: 20rem;
    height: 6px;
    background-image: linear-gradient(to left,v.$aOscuro 45%, transparent 50%,#6277a1 55%);
  }
}

@mixin btn ($color,$text, $hover ){
  border-radius: 20rem;
    text-transform: uppercase;
    font-weight: 600;
    padding: 1rem 2rem;
    background-color: $color;
    color: $text;


    &:hover {
      background-color: $hover;
    }
}