@use '../base/mixins' as m;
@use '../base/variables' as v;


.destacada {
  padding:7rem 0 2rem 0;
  background-image: linear-gradient(to bottom, transparent 60%, v.$azul  62%, v.$aOscuro 100% ), url(../img/destacada.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center, center center;

  

  @include m.phone {
    background-size: 100%, 100% 80%;
  }

  @include m.tablet {
    background-image: linear-gradient(to left, transparent 54%, v.$azul  55%, v.$aOscuro 100% ), url(../img/destacada.jpg);
    padding:13rem 0;
    background-size: 100%, 120% 130%;
   
  }

  @include m.pc {
    padding:16rem 0;
    background-size: 100%, 100% 130%;
    background-position: center, center center;
  }


  &__grid {
    display: grid;
    grid-template-rows: repeat(2,1fr);
    row-gap: 5rem;


    @include m.tablet {
      grid-template-rows: unset;
      gap: 0;
    }
  }

  &__contenido {
    padding-top: 7rem ;
    grid-row: 2/3;
    color: v.$blanco;

    @include m.tablet {
      padding-top: 0;
      width: 37%;
    }
  }

  &__heading {
    font-size: 2.5rem;
    text-transform: uppercase;
   
  }

  &__precio {
    font-size: 2rem;
    font-weight: 700;
    text-align: center;
  }

  &__enlace {
    @include m.btn( v.$blanco, v.$azul, v.$blanco );

  }

}