@use '../base/mixins' as m;
@use '../base/variables' as v;



.footer {
  padding: 2rem 0 0 0;
  background-color: v.$azul;
  color: v.$blanco;

  &__box {
    padding: 2rem 0;
    
  }

  &__heading {
    text-transform: uppercase;
    margin-bottom: 1.5rem;
  }

  


  &__nav {
    &__enlace {
      color: v.$blanco;
      margin-bottom: .7rem;
    }
    
  }

  &__text {
    padding: 0 2rem;
    text-align: justify;
  }


  &__copyright {
    background-color: v.$aOscuro;
    margin: 0;
    padding: 1.7rem 0;
    text-align: center;
  }

}

.footer__contenido {
  @include m.tablet {
    @include m.grid(3,1rem);
    align-items: center;
  }
}