
@use '../base/mixins' as m;
@use '../base/variables' as v;


.header {
  height: 45rem;
  background-image: linear-gradient(90deg,rgba(59, 72, 98, 0.751) 0,rgba(59, 72, 98, 0.82)),url(../img/header_bg.jpg);
  background-position: 50%;
  background-size: cover;
  padding: 3rem 0;
  position: relative;


  &__heading {
    @include m.tablet {
      display: flex;
      justify-content: space-between;
      align-items: center;
     }
   }



  &__icon {
    width: 20rem;
    margin: 0 auto;
    margin-bottom: 3rem;

    @include m.tablet {
      margin:0;
     }
  } 

  .nav {
    @include m.tablet {
     display: flex;
     gap: 1rem;
    }
   
     &__enlace {
       padding: .5rem;
       color: v.$blanco;

     }
   }


   &__info{
    margin-top: 8rem;
    color: v.$blanco;
  
  
    @include m.tablet{
      max-width: 50%;
      margin-top: 12rem;
    }
  }
  


  &__title {
    font-size: 3.5rem;
  }
  


  &__text {
    display: none;
  
    @include m.tablet {
      display:block;
    }
  }


  
  &__imagen {
   
    border-radius:10px 0 0 5rem ;
    display: none;
    position: absolute;

  
    @include m.tablet {
      display:block;
      max-width: 30rem;
      right: 0;
      bottom: -5rem;
    }

    @include m.pc {
      max-width: 45rem;
    }
  }
}





